import Component from '../react/component'

const ReactDOM = {
  render
}

function render(vnode, container) {
  return container.appendChild(_render(vnode, container))
}

function createComponent(comp, props) {

  let inst = null
  // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ï¼Œåˆ™åˆ›å»ºå®ä¾‹
  if (comp.prototype && comp.prototype.render) {
    inst = new comp(props)
  } else {
    // å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶ï¼Œå°†å‡½æ•°ç»„ä»¶è½¬æ¢ä¸ºç±»ç»„ä»¶ï¼Œæ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†
    inst = new Component(props)
    inst.constructor = comp
    // å®šä¹‰renderå‡½æ•°
    inst.render = function() {
      return this.constructor(props)
    }
  }

  return inst
}

function setComponentProps(comp, props) {
  if (!comp.base) {
    if (comp.componentWillMount) comp.componentWillMount()
  } else if (comp.componentWillReceiveProps) {
    comp.componentWillReceiveProps()
  }
  // è®¾ç½®ç»„ä»¶å±æ€§
  comp.props = props
  // æ¸²æŸ“ç»„ä»¶
  renderComponent(comp)
}


export function renderComponent(comp) {
  let base = null
  const renderer = comp.render() // jsxå¯¹è±¡
  base = _render(renderer) // jsèŠ‚ç‚¹å¯¹è±¡
  if (comp.base && comp.componentWillUpdate) {
    comp.componentWillUpdate()
  }
  if (comp.base) {
    if (comp.componentDidUpdate) comp.componentDidUpdate()
  } else if (comp.componentDidMount) {
    comp.componentDidMount()
  }
  if (comp.base && comp.base.parentNode) {
    comp.base.parentNode.replaceChild(base, comp.base)
  }
  comp.base = base

}

/**
 * å‡½æ•°renderæ¥æ”¶ä¸¤ä¸ªå‚æ•°
 * vnodeï¼Œä¸€ä¸ªæ˜¯jsxå¯¹è±¡ï¼ˆä¹Ÿå°±æ˜¯babelè½¬æ¢åçš„å¯¹è±¡ï¼‰
 * containerï¼Œç»è¿‡renderå†…é€»è¾‘å¤„ç†åè¢«æ’å…¥çš„èŠ‚ç‚¹
 */
function _render(vnode, container) {

  // å¦‚æœvnodeæ˜¯undefineã€nullã€booleanç±»å‹ï¼Œåˆ™æŠŠå®ƒç½®ä¸ºç©º
  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') {
    vnode = ''
  }

  if (typeof vnode === 'number') {
    vnode = String(vnode)
  }


  // å¦‚æœä¼ å…¥çš„vnodeæ˜¯ä¸€ä¸ªå­—ç¬¦ä¸²çš„è¯ï¼Œåˆ™åˆ›å»ºtextèŠ‚ç‚¹å¹¶æ’å…¥åˆ°æŒ‡å®šçš„èŠ‚ç‚¹åè¿”å›å³å¯
  if (typeof vnode === 'string') {
    return document.createTextNode(vnode)
  }

  // å¦‚æœvnodeæ˜¯ä¸€ä¸ªå‡½æ•°ï¼Œåˆ™æ¸²æŸ“ç»„ä»¶
  if (typeof vnode.tag === 'function') {
    // 1ã€åˆ›å»ºç»„ä»¶
    const comp = createComponent(vnode.tag, vnode.attrs)
    // // 2ã€è®¾ç½®ç»„ä»¶å±æ€§
    setComponentProps(comp, vnode, attrs)
    // // 3ã€ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
    return comp.base
  }

  // å¦‚æœvnodeæ˜¯å¯¹è±¡çš„è¯ï¼Œåˆ™å…ˆè·å–tagå’Œattrså±æ€§
  const {
    tag,
    attrs
  } = vnode

  const dom = document.createElement(tag)

  // å¦‚æœæœ‰è‡ªå®šä¹‰å±æ€§attrsï¼Œåˆ™éå†è¯¥å¯¹è±¡ï¼Œå°†å®ƒé€æ­¥è®¾ç½®åœ¨æŒ‡å®šçš„domä¸Šï¼Œè¿™é‡Œçš„domæ³¨æ„å¯èƒ½æ˜¯vodeç¬¬ä¸€çº§tagï¼Œä¹Ÿæœ‰å¯èƒ½æ˜¯childå†…çš„tagï¼Œå› ä¸ºä¼šåšé€’å½’
  if (attrs) {
    Object.keys(attrs).forEach((key) => {
      const value = attrs[key]
      setAttribute(dom, key, value)
    })
  }

  // éå†å­èŠ‚ç‚¹ï¼Œé€æ­¥é€’å½’åˆ°vnodeä¸ºç©ºä¸ºæ­¢
  if (vnode.childrens) {
    vnode.childrens.forEach((child) => {
      render(child, dom)
    })

  }


  return dom
}

/** 
 * å‡½æ•°setAttributeæ¥å—ä¸‰ä¸ªå‚æ•°
 * domï¼Œå°†è¦å¤„ç†çš„èŠ‚ç‚¹
 * keyï¼Œè®¾ç½®domå±æ€§çš„åç§°
 * valueï¼Œè®¾ç½®domå±æ€§çš„å€¼
*/
function setAttribute(dom, key, value) {

  // å°†å±æ€§åclassNameè½¬æ¢ä¸ºclass
  if (key === 'className') {
    key = 'class'
  }

  // å¦‚æœæ˜¯ä¸ªäº‹ä»¶(å› ä¸ºreactçš„æ£€æµ‹è§„åˆ™ï¼Œéœ€è¦ä»¥onå¼€å¤´)ï¼Œå°†å±æ€§ç›´æ¥è®¾ç½®è¿›domå†…
  if (/on\w+/.test(key)) {
    // è½¬æˆå°å†™
    key = key.toLowerCase()
    dom[key] = value || ''
  }

  //  å¦‚æœæ˜¯style
  else if (key === 'style') {

    // styleå¯èƒ½ä¼šæ˜¯å¯¹è±¡ï¼Œæˆ–è€…æ˜¯å­—ç¬¦ä¸²ï¼Œå¦‚æœæ˜¯å­—ç¬¦ä¸²çš„è¯ç›´æ¥è®¾ç½®csstext
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    }
    // å¦‚æœstyleæ˜¯å¯¹è±¡ï¼Œåˆ™éå†å¯¹è±¡å¹¶è®¾ç½®è¿›domå†…
    else if (value && typeof value === 'object') {

      for (let k in value) {
        // numberçš„è¯å¸®å®ƒåŠ å…¥å•ä½ï¼Œè¿™é‡Œæ˜¯ç®€å•å¤„ç†
        if (typeof value[key] === 'number') {
          dom.style[key] = value[k] + 'px'
        } else {
          dom.style[k] = value[k]
        }
      }
    }
  }
  // æ—¢ä¸äº‹ä»¶ï¼Œä¹Ÿä¸æ˜¯styleæ ·å¼
  else {

    // å¦‚æœè¿™ä¸ªå±æ€§domå·²ç»æœ‰äº†ï¼Œåˆ™ç›´æ¥æ›´æ–°
    if (key in dom) {
      dom[key] = value || ''
    }

    // æœ‰valueçš„è¯åˆ™è®¾ç½®å±æ€§å’Œå€¼
    if (value) {
      dom.setAttribute(key, value)
    }
    else {
      // æ²¡æœ‰çš„è¯åªè®¾ç½®å±æ€§
      dom.removeAttribute(key)
    }
  }

}

export default ReactDOM


